import React from 'react'
import Meta from 'components/Meta'
import styled from 'styled-components'

import theme from '@style/theme'
import { Button, H3, Form, Grid2_3 } from '@style/global'
import Collapse from '../components/collapse';
import Calendar from '../components/calendar';
import ImgUpload from '../components/img_upload';
import Poster from './poster';


class Info extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      title: null,
      subTitle: null,
      speakerName: null,
      speakerInfo: null,
      hostName: null,
      hostInfo: null,
      commentatorName: null,
      commentatorInfo: null,
      abtTalk: null,
      abtSpeaker: null,
      date: null,
      time: null,
      place: null,
      organizer: null,
      coOrganizer: null,
      url: null,
      pictures: null,
    };
    this.handleChange = this.handleChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
    this.coverCallback = this.coverCallback.bind(this);
  }

  handleCallback = (childData) =>{
    this.setState({
      date: childData.selectedDay,
      time: childData.time,
    });
  }

  coverCallback = (childData) =>{
    this.setState({
      pictures: childData.preview,
    })
    console.log("Image data is called:" + this.state.pictures )
  }

  handleChange(event) {
    const target = event.target;
    const value = target.value;
    const name = target.name;
    
    this.setState(
      {
        [name]: value
      }
    );
    console.log(this.state)
  }

  handleSubmit(event) {
    this.props.parentCallback(this.state);
    event.preventDefault();
    console.log("The current state is" + this.state.pictures )
  }

  onPrint(e) {
    e.preventDefault();
    window.print();
  }

  render() {
    return (
      <>
        <Meta title="週四社會學海報產生器" desc="週四社會學系列講座海報產生器"/>
        <H3><b>Poster Creator</b><br/><small>Department of Sociology, NTU</small></H3>
        <Form onSubmit={this.handleSubmit}>
          <InputBox>
            <Collapse title="講座主題" key="Topic" open='true'>
              <InputItem>
                <label htmlFor="title">標題</label>
                <input id="title" type="text" name="title" value={this.state.title} onChange={this.handleChange} placeholder="Title"/>
              </InputItem>
              <InputItem>
                <label htmlFor="subTitle">副標題</label>  
                <input id="subTitle" type="text" name="subTitle" value={this.state.subtitle} onChange={this.handleChange} placeholder="Sub-title"/>
              </InputItem>
            </Collapse>
            <Collapse title="講者與主持" key="Speaker & Host">
              <InputItem>
                <label htmlFor="speaker">講者</label>
                <Grid2_3>
                <input type="text" name="speakerName" value={this.state.speakerName} onChange={this.handleChange} placeholder="Name"/>
                <input type="text" name="speakerInfo" value={this.state.speakerInfo} onChange={this.handleChange} placeholder="Info"/>
                </Grid2_3>
              </InputItem>
              <InputItem>
                <label htmlFor="host">主持</label>
                <Grid2_3>
                <input type="text" name="hostName" value={this.state.hostName} onChange={this.handleChange} placeholder="Name"/>
                <input type="text" name="hostInfo" value={this.state.hostInfo} onChange={this.handleChange} aria-label="Host Info" placeholder="Info"/>
                </Grid2_3>
              </InputItem>
            </Collapse>
            <Collapse title="與談人" key="Commentator">
              <InputItem>
                <label htmlFor="Commentator">與談人</label>
                <Grid2_3>
                  <input type="text" name="commentatorName" value={this.state.commentatorName} onChange={this.handleChange} placeholder="Name (Optional)"/>
                  <input type="text" name="commentatorInfo" value={this.state.commentatorInfo} onChange={this.handleChange} aria-label="Commentator Info" placeholder="Info (Optional)"/>
                </Grid2_3>
              </InputItem>
            </Collapse>
            <Collapse title="關於" key="About">
              <InputItem>
                <label htmlFor="abtTalk">講座介紹</label>
                <input type="text" name="abtTalk" value={this.state.abtTalk} onChange={this.handleChange} placeholder="Desciption about the talk (Optional)"/>
              </InputItem>
              <InputItem>
                <label htmlFor="abtSpeaker">講者介紹</label>
                <input type="text" name="abtSpeaker" value={this.state.abtSpeaker} onChange={this.handleChange} placeholder="Desciption about the speaker (Optional)"/>
              </InputItem>
            </Collapse>
            <Collapse title="時間與地點" key="Time & Place" overflowWhenOpen="visible">
              <InputItem>
                <label htmlFor="time">時間</label>
                <Grid2_3>  
                  <Calendar parentCallback = {this.handleCallback}/>
                  <InputItem>
                    <label htmlFor="place">地點</label>
                    <select name="place" onBlur={this.handleChange} value={this.state.place} placeholder="Please select a place">
                      <option value="Room 401">Room 401</option>
                      <option value="Room 101">Room 101</option>
                      <option value="Room 319">Room 319</option>
                      <option value="Room 318B">Room 318B</option>
                    </select>
                  </InputItem>
                </Grid2_3>
              </InputItem>
            </Collapse>
            <Collapse title="主辦單位" key="Organizer">
              <InputItem>
                <label htmlFor="Org">主辦單位</label>
                <input type="text" name="organizer" value={this.state.organizer} onChange={this.handleChange} placeholder="Organizer"/>
              </InputItem>
              <InputItem>
                <label htmlFor="coOrg">協辦單位</label>
                <input type="text" name="coOrganizer" value={this.state.coOrganizer} onChange={this.handleChange} placeholder="Co-organizer (Optional)"/>
              </InputItem>
            </Collapse>
            <Collapse title="書封上傳與 QR Code" key="Other">
              <InputItem>
                <label htmlFor="qr">QR Code</label>
                <input type="url" name="url" value={this.state.url} onChange={this.handleChange} placeholder="Please enter a URL (Optional)"/>
              </InputItem>
              <InputItem>
                <label htmlFor="book">書封</label>
                <ImgUpload parentCallback = {this.coverCallback}/>
              </InputItem>
            </Collapse>
          </InputBox>
          <Button BorderlessLight>
            <input type="submit" value="Preview"/>
          </Button>
          <Button BorderlessDark onClick={this.onPrint}>
            Print
          </Button>
        </Form>
      </>
    );
  }
}


class Generator extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      title: null,
      subTitle: null,
      speakerName: null,
      speakerInfo: null,
      hostName: null,
      hostInfo: null,
      commentatorName: null,
      commentatorInfo: null,
      abtTalk: null,
      abtSpeaker: null,
      date: null,
      time: null,
      place: null,
      organizer: null,
      coOrganizer: null,
      url: null,
      pictures: null,
    };
  }

  handleCallback = (childData) =>{
    this.setState({
      title: childData.title, 
      subTitle: childData.subTitle,
      speakerName: childData.speakerName,
      speakerInfo: childData.speakerInfo,
      hostName: childData.hostName,
      hostInfo: childData.hostInfo,
      commentatorName: childData.commentatorName,
      commentatorInfo: childData.commentatorInfo,
      abtTalk: childData.abtTalk,
      abtSpeaker: childData.abtSpeaker,
      date: childData.date,
      time: childData.time,
      place: childData.place,
      organizer: childData.organizer,
      coOrganizer: childData.coOrganizer,
      url: childData.url,
      pictures: childData.pictures,
    })
  }

  render(){
    return (
      <Grid>
        <Input>
          <Info parentCallback = {this.handleCallback}/>
        </Input>
        <Output>
          <LifeView data={this.state}/>
        </Output>
      </Grid>
    )
  }
}


const LifeView = ({data}) =>  (
  <LifeViewBox>
    <Poster 
      title={data.title} 
      subTitle={data.subTitle}
      speakerName= {data.speakerName}
      speakerInfo= {data.speakerInfo}
      hostName= {data.hostName}
      hostInfo= {data.hostInfo}
      commentatorName= {data.commentatorName}
      commentatorInfo= {data.commentatorInfo}
      abtTalk= {data.abtTalk}
      abtSpeaker= {data.abtSpeaker}
      date= {data.date}
      time= {data.time}
      place= {data.place}
      organizer= {data.organizer}
      coOrganizer= {data.coOrganizer}
      url= {data.url}
      pictures= {data.pictures}
    />
  </LifeViewBox>  
);



export default Generator

